            -->



            <br>
            <br>
<!--            <h5 v-if="email != 'none'">{{ email }}<br></h5>
            <h1 v-if="profession != 'none'" style="font-size:5vw;letter-spacing:0.8vw">{{profession.toUpperCase() }}</h1><br><br>-->
            <div class="text-xs-center" v-if="diagram_number == 2" @click="toggleChart()">
                <v-progress-circular
                  :rotate="-90"
                  :size="250"
                  v-bind:width="propertyWidth[0]" 
                  :value="(safety/MAX)*100"
                  v-bind:color="propertyColor[0]"
                >
                  TRYGGHET & SKERHET <h1>{{ parseInt((safety / MAX) * 100)}}%</h1>
                </v-progress-circular>
                <v-progress-circular
                  :rotate="-90"
                  :size="250"
                  v-bind:width="propertyWidth[1]" 
                  :value="(variation/MAX)*100"
                  v-bind:color="propertyColor[1]"
                >
                  VARIATION <h1>{{ parseInt((variation / MAX) * 100)}}%</h1>
                </v-progress-circular>

                <v-progress-circular
                  :rotate="-90"
                  :size="250"
                  v-bind:width="propertyWidth[2]" 
                  :value="(meaning/MAX)*100"
                  v-bind:color="propertyColor[2]"
                >
                  BETYDELSEFULL <h1>{{ parseInt((meaning / MAX) * 100)}}%</h1>
                </v-progress-circular>

                <v-progress-circular
                  :rotate="-90"
                  :size="250"
                  v-bind:width="propertyWidth[3]" 
                  :value="(love/MAX)*100"
                  v-bind:color="propertyColor[3]"
                >
                  KRLEK <h1>{{ parseInt((love / MAX) * 100)}}%</h1>
                </v-progress-circular>

                <v-progress-circular
                  :rotate="-90"
                  :size="250"
                  v-bind:width="propertyWidth[4]" 
                  :value="(development/MAX)*100"
